<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list :IsShowAction="false" :showCheckBox="true" :listType="[DetailListType]" ModuleName="Approval"
            SubModuleCode="Timer" IdentityColumn="RULEID" :HeaderText="$t('ApprovalRule')" :ListData="RuleData"
            :HeaderData="Headers" :widgets="widgets" :callbackfunction="FetchData" :HeaderButtons="listheaderbuttons"
            :TotalRecords="TotalRecords" :SearchFields="leftSearchFields" :RenderRowActionMethod="renderActions"
            :ListDataCallBackFunction="FetchData" :LegendArray="LegendArray" :SortExp="SortExp" :SortBy="SortBy"
            :NorecordfoundText="$t('NoRecordfound')" v-on:LeftsearchButtonClick="leftCommonSearch"
            v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
            v-on:ActionButtonClick="actionButtonClick" v-on:SortdataButtonClick="sortdata">

            <template v-slot:slotdata="{ data }">
                <template v-if="data.column.COLUMN_NAME == 'STATUS'">
                    <td class="text-center single-action" :title="data.row.STATUSID == 1001 ? 'Active' : 'Inactive'">
                        <div class="cstm-drop-btn">
                            <select v-model="data.row.STATUSID"
                                @change="DrpdownUpdateStatus(data.row.STATUSID, data.row.RULEID,);"
                                :class="data.row.STATUSID == 1001 ? 'ddlupdatestatus w-80 btn-success' : 'ddlupdatestatus w-80 btn-danger'">
                                <option value="1001" selected="selected">Active</option>
                                <option value="1002">Inactive</option>
                            </select>
                        </div>
                    </td>
                </template>
                
                <template v-if="data.column.COLUMN_NAME == 'RULENAME'">
                    <span class="text-dark" v-if="data.row.RULENAME">
                        <a href="javascript:;" v-on:click="EditRule(data.row.RULEID)" :id="data.row.RULEID"
                            ><em class="text-primary" aria-hidden="true">{{ data.row.RULENAME
                            }}</em></a>
                    </span>

                </template>
            </template>

        </tg-list>
    </section>
</template>

<script>
import DataService from '../../service/DataService';
import Schema from '../../views/ApprovalRule/ApprovalRuleSchema.js'

export default {

    data() {
        return {
            widgets:[],
            LegendArray:[],
            CategorytList: [],
            isLoading: false,
            leftSearchFields: Schema.leftsearchSchema,
            listActions: Schema.listActions,
            Headers: [
                {
                    "COLUMN_NAME": 'RULENAME',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('RuleName'),
                    "DISPLAY_ORDER": 1,
                    settings: {
                        isInSlot: true,
                    }

                },
                {
                    "COLUMN_NAME": 'MODULE',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Module'),
                    "DISPLAY_ORDER": 2
                },
                {
                    "COLUMN_NAME": 'LIMITEDDURATION',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Duration'),
                    "DISPLAY_ORDER": 3
                },
                {
                    "COLUMN_NAME": 'USERS',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Users'),
                    "DISPLAY_ORDER": 4,
                    // settings: {
                    //     isInSlot: true,
                    // }
                },
                {
                    "COLUMN_NAME": 'APPROVALCHAINNAME',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('ApprovalChain'),
                    "DISPLAY_ORDER": 5,
                    // settings: {
                    //     isInSlot: true,
                    // }
                },
                {
                    "COLUMN_NAME": 'APPROVERS',
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Approvers'),
                    "DISPLAY_ORDER": 6,
                    // settings: {
                    //     isInSlot: true,
                    // }
                },
                {
                    "COLUMN_NAME": "STATUS",
                    "DATA_TYPE": "text",
                    "SORTABLE": false,
                    "VISIBLE": true,
                    "DISPLAY_NAME": this.$t('Active'),
                    "DISPLAY_ORDER": 7,
                    settings: {
                        isInSlot: true
                    }
                },

            ],
            noRecord: true,
            statusIds: '',
            LocationId: '',
            IsDefault: '',
            RuleData: [],
            TotalRecords: 0,
            PageNumber: 1,
            CurrentPage: 1,
            TotalPages: 0,
            StatusData: [],
            listheaderbuttons: Schema.buttons,
            DetailListType: 'List',
            ruleName: '',
            moduleIds: '',
            approvalChainIds: '',
            userIds: '',
            approverIds: '',
            statusIds: '',
            sortBy: '',
            sortExp: '',
            PageSize: 10,
            isPartial: 0

        }
    },
    created() {
        this.FetchData();
        Schema.bindleftCommonSearchdropdown(this, 'Approval', 'APPROVALRULE');
        Schema.bindleftCommonSearchdropdown(this, "Approval", "ApprovalChain");
        Schema.bindleftCommonSearchdropdown(this, "Approval", "Module");
        //Schema.bindleftCommonSearchdropdown(this, 'Asset', 'Sub_Module_Name');
    },
    methods: {
        async FetchData() {
            var vueObj = this;
            var url = `${vueObj.searchCondition}&ruleName=${vueObj.ruleName}&moduleIds=${vueObj.moduleIds}&approvalChainIds=${vueObj.approvalChainIds}&userIds=${vueObj.userIds}&approverIds=${vueObj.approverIds}&statusIds=${vueObj.statusIds}&sortBy=${vueObj.sortBy}&sortExp=${vueObj.sortExp}&pageSize=${vueObj.PageSize}&pageNum=${vueObj.PageNumber}&isPartial=${vueObj.isPartial}`;
            DataService.ApprovalRuleListing(url).then(response => {
                if (response.data != null) {
                    if (response.data.length > 0) {

                        vueObj.RuleData = response.data;

                        vueObj.RuleData.forEach((row) => {
                            row.isCheckBoxDisabled = false;


                            if (row.UCOUNT == 0) {
                                row.isCheckBoxDisabled = false;
                            }
                            else {
                                row.isCheckBoxDisabled = true;
                            }
                        })
                        vueObj.TotalRecords = response.data[0].TotalRecords
                        vueObj.TotalPages = Math.ceil(vueObj.TotalRecords / vueObj.PageSize);
                        vueObj.CurrentPage = vueObj.PageNumber;
                        vueObj.noRecord = false;

                    } else {
                        vueObj.RuleData = [];
                        vueObj.noRecord = true;
                        vueObj.TotalRecords = 0;
                    }
                } else {
                    vueObj.noRecord = true;
                    vueObj.RuleData = [];
                    vueObj.TotalRecords = 0
                }
                setTimeout(function () {
                    vueObj.CheckBoxBootstrap()
                }, 100);
            });
            vueObj.isLoading = false;
        },
        EditRule: function (RULEID) {
            
            this.$router.push({
                name: 'ApprovalRuleAdd',
                params: {
                    id: RULEID
                }
            });
        },
        DrpdownUpdateStatus(statusId, id) {
            var vm = this;
            vm.isLoading = true;
            vm.confirmR(vm.$t('Are you sure to update this record?'), true, false, function (result) {
                var url = `id=${id}&statusId=${statusId}&tablename=TALYGEN_APPROVAL_RULE&columnname=APPROVAL_RULE_ID`;
                DataService.UpdateStatus(url).then(response => {
                    var status = ''
                    vm.ShowAlert(vm.$t('UpdateStatus'), "success", ('Alert'));
                    vm.FetchData();
                    vm.isLoading = false;
                })
            })
            vm.FetchData();
            vm.isLoading = false;
        },
        DeleteRule() {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            });
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  "), function () {
                    var postJSON = {
                        ids: val,
                        tblname: "TALYGEN_APPROVAL_RULE",
                        primarycolname: "APPROVAL_RULE_ID"
                    };
                    vm.isLoading = true;
                    var url = `ids=${val}`
                    DataService.DeleteRule(url).then(response => {
                        if (response.data) {
                            $("[id^='chkAll']").prop("checked", false).removeAttr('checked');
                            $('.chkItems:checkbox:checked').prop("checked", false).removeAttr('checked');
                            $("[id^='DeleteMultiple']").removeClass('enable').addClass('disabled');
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('ApprovalRule')]), "Success", true, vm.$t('Alert'));
                            vm.FetchData();
                        } else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Group_Item')]), "failure", true, vm.$t('Alert'));
                        }
                        vm.isLoading = false;
                    });
                });
            }
        },
        actionButtonClick(callbackfunction, item) { },
        renderActions(row) { },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchData();
        }, 
        commonHeaderButtonClick(event) {
            debugger;
            switch (event.callbackfunction) {
                case "DeleteRule":
                    this.DeleteRule()
                    break;
                    case "setTourGuideSteps":
                    this.setTourGuideSteps(0);
                    break;
                case "AddRule":
                    this.AddNewRule()
                    break;
            }
        },
        AddNewRule: function () {
            this.$router.push({
                name: 'ApprovalRuleAdd',
            });
        },

        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                this.searchCondition = value.searchConditionJson;
            } else {
                this.searchCondition = "";
            }
            this.FetchData();
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                } else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                } else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchData();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },
    }
}
</script>
